export default {"ts":"import { Attributes, Component } from 'jinge';\n\nimport _tpl from './link-buttons.html';\n\nexport default class ExapmleLinkButtons extends Component {\n  static template = _tpl;\n\n  pageUrl: string;\n\n  constructor(attrs: Attributes) {\n    super(attrs);\n    this.pageUrl = window.location.href;\n  }\n}\n","sass":"","html":"<!-- import style from './style.scss' -->\n<div :class=\"style.demo\">\n  <div>\n    <small>Buttons</small>\n    <md-button>Default</md-button>\n    <md-button class=\"md-primary\">Primary</md-button>\n    <md-button class=\"md-accent\">Accent</md-button>\n    <md-button disabled>Disabled</md-button>\n  </div>\n\n  <div>\n    <small>Links</small>\n    <md-button e:href=\"pageUrl\">Default</md-button>\n    <md-button e:href=\"pageUrl\" class=\"md-primary\">Primary</md-button>\n    <md-button e:href=\"pageUrl\" class=\"md-accent\">Accent</md-button>\n    <md-button e:href=\"pageUrl\" disabled>Disabled</md-button>\n    <md-button href=\"https://www.google.com\" target=\"_blank\">Open in new tab</md-button>\n  </div>\n</div>"}