export default {"ts":"import { Attributes, Component } from 'jinge';\n\nimport _tpl from './regular-buttons.html';\n\nexport default class ExampleRegularButtons extends Component {\n  static template = _tpl;\n\n  loading: boolean;\n\n  constructor(attrs: Attributes) {\n    super(attrs);\n    this.loading = false;\n  }\n\n  onClick(evt: MouseEvent) {\n    // eslint-disable-next-line no-console\n    console.log('click button', evt);\n  }\n\n  load() {\n    this.loading = true;\n    setTimeout(() => {\n      this.loading = false;\n    }, 3000);\n  }\n}\n","sass":"","html":"<div style=\"background-color: var(--demo-states-background); padding: 12px;\">\n  <div>\n    <small>Basic Buttons</small>\n    <md-button on:click=\"onClick\">\n      <md-icon-home/>\n      <span>Default</span>\n    </md-button>\n    <md-button e:ripple=\"false\">Ripple Off</md-button>\n    <md-button on:click=\"onClick\" class=\"md-primary\">Primary</md-button>\n    <md-button class=\"md-accent\">Accent</md-button>\n    <md-button disabled>Disabled</md-button>\n    <md-button class=\"md-primary\" disabled>Primary Disabled</md-button>\n  </div>\n\n  <div>\n    <small>Raised Buttons</small>\n    <md-button class=\"md-raised\">\n      <md-icon-home/>\n      <span>Default</span>\n    </md-button>\n    <md-button class=\"md-raised\" e:ripple=\"false\">Ripple Off</md-button>\n    <md-button on:click=\"load\" e:disabled=\"loading\" class=\"md-raised md-primary\">Primary</md-button>\n    <md-button class=\"md-raised md-accent\">Accent</md-button>\n    <md-button class=\"md-raised\" disabled>Disabled</md-button>\n    <md-button class=\"md-raised md-primary\" disabled>Primary Disabled</md-button>\n  </div>\n  <div>\n    <small>Flat Buttons</small>\n    <md-button class=\"md-flat\">\n      <md-icon-home/>\n      <span>Default</span>\n    </md-button>\n    <md-button class=\"md-flat\" e:ripple=\"false\">Ripple Off</md-button>\n    <md-button on:click=\"load\" e:disabled=\"loading\" class=\"md-flat md-primary\">Primary</md-button>\n    <md-button class=\"md-flat md-accent\">Accent</md-button>\n    <md-button class=\"md-flat\" disabled>Disabled</md-button>\n    <md-button class=\"md-flat md-primary\" disabled>Primary Disabled</md-button>\n  </div>\n  <div>\n    <small>Stroked Buttons</small>\n    <md-button class=\"md-stroked\">\n      <md-icon-home/>\n      <span>Default</span>\n    </md-button>\n    <md-button class=\"md-stroked\" e:ripple=\"false\">Ripple Off</md-button>\n    <md-button on:click=\"load\" e:disabled=\"loading\" class=\"md-stroked md-primary\">Primary</md-button>\n    <md-button class=\"md-stroked md-accent\">Accent</md-button>\n    <md-button class=\"md-stroked\" disabled>Disabled</md-button>\n    <md-button class=\"md-stroked md-primary\" disabled>Primary Disabled</md-button>\n  </div>\n  \n  <div>\n    <small>Dense</small>\n    <md-button class=\"md-dense md-primary\">Flat</md-button>\n    <md-button class=\"md-dense md-raised md-primary\">Raised</md-button>\n  </div>\n</div>"}